@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.landing{
    /* width: 100 vw; */
    /* height: 100 vh; */
    display: grid;
    grid-template-columns: 1fr 320px 1fr;
}
.game{
    background-image: url(/static/media/StoneWall_TXR.53ad008e.png);
    height: 100vh;
    background-color: aqua;
    background-size: contain;
    /* background-attachment: local; */
    overflow: hidden;/* auto for testing change to hidden  */
}
#player {

    position: absolute;
    margin: 0;
    width: 40px;
    height: 40px;
    background-color: black;
    color: crimson;
    overflow: hidden;
}

#player>img {
    width: 48px;
} 
.deathzone {
    background-color: transparent;
    position: absolute;
    width: 320px;
    height: 50px;
    /* margin: 808px 0 0 0; */

}

.deathzone>div {
    overflow: hidden;
    width: 54px;
    height: 50px;
}

.deathzone img {
    width: 653%;
    margin: -184px 0 0 -197px;
}
.left--section {
    background-color: khaki;
    z-index: 2;
}

h1 {
    width: 142px;
    margin: 108px 0px 0px 67px;
    font-size: 3rem;
}
.section--right {
    background-color: khaki;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    /* justify-content: flex-end; */
}

aside {
    /* margin-right: 40px; */
}

.aside--top {
    width: 100%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.aside--top>* {
    margin: 26px 50px;  
}

.aside--bottom {
    width: 100%;
    height: 50%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
}

.aside--bottom>* {
    margin: 26px 50px;
}

.modal {
    border-radius: 0%;

    display: none;
    position: absolute;

    background-color: brown;
    width: 490px;
    width: 48%;
    /* width: 37%; */
    height: 50%;
    margin: 13% 27% 0 0;

    /* align-items: flex-start; */
    justify-content: space-evenly;

}

.user--modal {

    /* display: none;
    position: absolute;

    background-color: brown;
    width: 37%;
    height: 50%;
    margin: 13% 31% 0 0;

    align-items: flex-start;
    justify-content: space-evenly; */
}

.user--modal>.signup{

    width: 50%;
}

.user--modal>.login{
    width: 50%;
}

.form--input {
    display: flex;
    flex-direction: column;
    margin: 22px 0px;
    padding: 0 10px;
}

.user--modal--logged {
    flex-wrap: wrap;

    /* display: none;
    position: absolute;

    background-color: brown;
    width: 37%;
    height: 50%;
    margin: 13% 31% 0 0;

    align-items: flex-start;
    justify-content: space-evenly; */
}

.user--modal--logged>div {
    width: 65px;
    margin: 100px 0 0 0;
    height: 65px;
}

.edit--user {

    display: flex;
    flex-wrap: wrap;
}

.edit--user>input {
    
    margin: 0 0 0 12px;
}

.add--friend {

    width: 53%;
    margin: 0 0px 0 -19px;
}

.add--friend>input {
    
    margin: 0 0 0 12px;
}

.gameover--score--modal {

    /* display: flex; */
    flex-flow: column;
    align-items: center;
}


.gameover--score--modal>div {

    margin: 0 0 0 0;
    width: 96%;
    height: 36%;
    display: flex;
    flex-flow: column;
    align-items: center;
}

.gameover--highscore--modal {

    /* display: flex; */
    flex-flow: column;
    align-items: center;
}


.gameover--highscore--modal>div {

    margin: 0 0 0 0;
    width: 96%;
    height: 36%;
    display: flex;
    flex-flow: column;
    align-items: center;
}
 
.user--modal>form {
    display: flex;
    flex-flow: column;
}

.user--modal input {
    margin: 0 10px;
}

.signup div:first-child {
    margin: 39px 0 23px 0;
}

.login div:first-child {
    margin: 125px 0 23px 0;
}

button {
    font-size: 1.5rem;
}

.icon {
    font-size: 68px;
}
.controls--modal {
    flex-flow: column;
}

.controls--modal>* {
    margin: auto;
}

.controls--modal>div {
    width: 96%;
    height: 50%;
}


.leaderboard--modal {
    flex-flow: column;
    align-items: center;
}


.leaderboard--modal>div {
    width: 100%;
    height: 75%;
}

.leaderboard--modal table{
    margin: auto;  
    max-height: 100%;
    width: 79%;
    border-style: inset;
}

.leaderboard--modal th{
    text-align: inherit;
    margin: auto;  
    max-height: 100%;
}
